/* app.css */

/* Genel Stil */
body {
  font-family: 'Roboto', sans-serif; /* Modern bir font */
  background-color: #f4f4f9; /* Hafif gri arka plan */
  color: #333; /* Genel metin rengi */
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  position: relative; /* Modal ve diğer öğelerin konumlandırması için */
}

/* Kullanıcı Listesi */
.user-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start; /* Sol hizalı */
  gap: 15px; /* Elemanlar arasında boşluk */
  padding: 20px; /* Genel listeye biraz boşluk ekleyelim */
}

.user-rectangle {
  width: 200px; /* Biraz daha geniş yapalım */
  height: 100px; /* Daha büyük */
  border-radius: 12px;
  background-color: #007BFF; /* Mavi arka plan */
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  color: white;
  font-size: 16px; /* Yazı boyutunu artırdık */
  font-weight: 500; /* Kalınlık biraz daha az */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Hafif gölge ile modern bir görünüm */
  transition: transform 0.2s ease-in-out; /* Hover efekti */
  position: relative; /* Menü simgesi için */
}

.user-rectangle:hover {
  transform: translateY(-5px); /* Hover'da yukarı kaldır */
  background-color: #0056b3; /* Hover'da koyulaşan mavi */
}

.user-rectangle .email {
  margin-left: 10px;
}

.user-rectangle .menu-icon {
  cursor: pointer;
  font-size: 24px; /* Daha büyük */
  position: absolute;
  bottom: 10px;
  right: 10px;
  color: white;
}

/* Modern Buton Stili */
.button {
  background-color: #007BFF;
  color: white;
  border: none;
  padding: 12px 20px;
  font-size: 16px;
  font-weight: bold;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 10px;
}

.button:hover {
  background-color: #0056b3;
}

/* Modal Stil */
.Modal {
  background-color: white;
  border-radius: 10px;
  padding: 20px;
  max-width: 500px;
  margin: 0 auto;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.Modal h2 {
  margin-top: 0;
  font-size: 22px;
  color: #333;
}

/* Mesajlaşma Ekranı */
.chat-title {
  font-size: 24px;
  margin-bottom: 20px;
  color: #007BFF;
  text-align: center;
}

.message-box {
  border: 1px solid #ddd;
  padding: 12px;
  border-radius: 8px;
  margin-bottom: 10px;
  width: 100%;
}

.chat-item {
  padding: 10px;
  background-color: #f1f1f1;
  margin-top: 5px;
  border-radius: 8px;
  max-width: 60%;
}

.chat-item:nth-child(odd) {
  background-color: #e1e1e1; /* Alternatif arka plan rengi */
}

.chat-item .message-user {
  font-weight: bold;
  color: #007BFF;
}

/* Giriş Ekranı */
.input {
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 6px;
  margin-bottom: 15px;
  width: 100%;
  font-size: 16px;
}

/* Modal ve Menü */
.context-menu {
  position: absolute;
  background-color: #ffffff;
  border: 1px solid #ddd;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  width: 180px;
  border-radius: 8px;
  padding: 10px;
  display: none; /* Başlangıçta gizli */
}

.context-menu.visible {
  display: block; /* Görünür olduğunda menüyü göster */
}

.context-menu-item {
  padding: 8px 12px;
  cursor: pointer;
  font-size: 14px;
  color: #333;
  display: flex;
  align-items: center;
}

.context-menu-item:hover {
  background-color: #f0f0f0;
}

.context-menu-item .icon {
  margin-right: 10px;
}
